import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { ThemeProvider } from '@mui/material/styles';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import CssBaseline from '@mui/material/CssBaseline';

import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import ReactMarkdown from 'react-markdown';
import OptionBox from './OptionBox';
import theme from './them';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      style={{ flexGrow: 2 }} 
      className='scrollable-container'
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3  , justifyContent: 'center' }}  >
          {children}
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const  VerticalTabs = () => {
  const [value, setValue] = useState(0);
  
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <ThemeProvider theme={theme}>
       <CssBaseline />
    <Box sx={{ bgcolor: '#3f3e3e', display: 'flex', height: '90vh'  }}>

        <Tabs
          orientation="vertical"
          value={value}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          sx={{ borderRight: 1, borderColor: 'divider' ,
            minWidth: '200px'  }}
        >
          <Tab sx = {{color: "white"}} label="Introduction" {...a11yProps(0)} />
     
          <Tab sx = {{color: "white"}} label="Result" {...a11yProps(1)} />
        </Tabs>
 
      
        <TabPanel value={value} index={0} sx={{ padding: 2, overflowY: 'auto' }}>
        <section id="introduction">
          <Typography variant="h4" component="h1" gutterBottom>
            Introduction
          </Typography>
          <Typography variant="body1"  sx={{ textAlign: 'left'  }}>
          The Capital Asset Pricing Model (CAPM) has been a cornerstone of modern finance, providing insights into the relationship between systematic risk and returns. It has inspired factor models that consider additional elements like firm size and economic conditions. Extensions to CAPM, including international and incomplete markets, reveal significant differences in traditional and incomplete market betas.
          </Typography>
          <Typography variant="body1" paragraph gutterBottom sx={{ textAlign: 'left' }}>
            Despite their influence, traditional models often assume static relationships, failing to capture dynamic market conditions. Recent evidence highlights the need for more adaptive frameworks.
          </Typography>
          <Typography variant="body1" paragraph gutterBottom sx={{ textAlign: 'left'  }}>
          Advances in machine learning, such as Generalized Random Forests (GRFs), offer more flexible modeling of nonlinear relationships in financial data. GRFs incorporate local linear regressions and condition on external variables, enhancing traditional linear factor models.
          </Typography>
          <Typography variant="body1" paragraph gutterBottom sx={{ textAlign: 'left'  }}>
          This study integrates GRFs into the asset pricing framework, testing conditional CAPM and conditional Fama-French models. These models adjust their betas based on market conditions, improving their explanatory power for asset returns across 25 portfolios. Our findings support the potential of this methodology to enhance financial modeling, risk assessment, and portfolio optimization.
          </Typography>
       
          
        </section>
        </TabPanel>
        
        <TabPanel value={value} index={1}  >
        
        <OptionBox/>
       
        </TabPanel>
 
    </Box>
    </ThemeProvider>
  );
}

export default VerticalTabs;