import Plot from 'react-plotly.js';

const VIXPlot = ({data , VIX  , method}) => {
   
    if (!data) {
      return <div>Loading...</div>;
    }
    const methods = ['R square' , 'Beta']
    // Extract VIX and other columns
    const selectedTitle = methods[method]
 
    const vix = VIX
    const columns = Object.keys(data).filter(col => col !== 'VIX');
    const traces = columns.map(column => ({
      x: vix,
      y: Object.values(data[column]),
      type: 'scatter',
      mode: 'lines',
      name: column,
    }));
  
    return (
      <Plot
        data={traces}
        layout={{
            title: 'Conditional R square for CAPM and conditional CAPM ',
            xaxis: { title: 'VIX', color: '#ffffff' },
            yaxis: { title: {selectedTitle} , color: '#ffffff' },
            plot_bgcolor: 'transparent',
            paper_bgcolor: 'transparent',
            width: 1000,
            height: 500,
            font: {
              color: '#ffffff',
            },
            title: {
              text: '',
              font: {
                size: 24,
                color: '#ffffff',
              },
            },
           
            margin: {
              l: 50,
              r: 50,
              b: 50,
              t: 50,
            },
            hovermode: 'closest',
          }}
          config={{
            displayModeBar: false, // Hide the toolbar
          }}
      />
    );
  };
  
  export default VIXPlot;