import React, { useState , useEffect} from 'react';
import Box from '@mui/material/Box';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import axios from 'axios';
import VIXPlot from './VixPlot';
import { ThemeProvider } from '@mui/material/styles';
import theme from './them';
// Example images



const portfolios = ['SMALL LoBM', 'ME1 BM2', 'ME1 BM3', 'ME1 BM4', 'SMALL HiBM', 'ME2 BM1',
    'ME2 BM2', 'ME2 BM3', 'ME2 BM4', 'ME2 BM5', 'ME3 BM1', 'ME3 BM2',
    'ME3 BM3', 'ME3 BM4', 'ME3 BM5', 'ME4 BM1', 'ME4 BM2', 'ME4 BM3',
    'ME4 BM4', 'ME4 BM5', 'BIG LoBM', 'ME5 BM2', 'ME5 BM3', 'ME5 BM4',
    'BIG HiBM']


const binary = [0 , 1]   
const models = ['CAPM' , 'Fama-French 3 factor']
const methods = ['R square' , 'Beta']
let count = 0 




const OptionBox = () => {
 
  const [chartData, setChartData] = useState();
  const [selectedData , setSelectedData] = useState();
  const [selectedModel , setSelectedModel] = useState(0)
  const [selectedMethod , setSelectedMethod] = useState(0)
  const [selectedOption, setSelectedOption] = useState('SMALL LoBM');
  const handleChange = (event) => {
    setSelectedOption(event.target.value);
  };
  const handleChangeModel = (event) =>{
  setSelectedModel(event.target.value)
  }
  const handleChangeMethod = (event) =>{
    setSelectedMethod(event.target.value)
  }
  let vix;
  useEffect(() => {
    const fetchJsonFiles = async () => {
      try {
        const urls = ['https://github.com/RiskLabAI/Notebooks.py/blob/main/input_data/vixdailycapm.json' , 'https://github.com/RiskLabAI/Notebooks.py/blob/main/input_data/vixdailycapmbeta.json' , 'https://github.com/RiskLabAI/Notebooks.py/blob/main/input_data/vixdaily3factor.json' , 'https://github.com/RiskLabAI/Notebooks.py/blob/main/input_data/vixdaily3factbeta.json'];
        const requests = urls.map(url => axios.get(url));
        const responses = await Promise.all(requests);
        const jsonData = responses.map(response => response.data);
        setChartData(jsonData);
        setSelectedData(JSON.parse(jsonData[0][selectedOption]))
        vix = JSON.parse(jsonData[0][selectedOption])['VIX']
      } catch (error) {
        console.error("Error fetching the JSON data", error);
      }
    };
    fetchJsonFiles();
  }, []);


  useEffect(() => {
    (chartData && setSelectedData(JSON.parse(chartData[ parseInt( selectedModel *2 + selectedMethod)][selectedOption])))
  
  }, [selectedOption , selectedModel , selectedMethod]);
  
  


  
  return (
    <Box>
      <ThemeProvider theme={theme}>
      <FormControl variant="outlined" sx={{ minWidth: 200, mb: 2 }}>
        <InputLabel id="model-select-label" sx={{ color : "white"}}>Select Model</InputLabel>
        <Select
          labelId="model-select-label"
          value={selectedModel}
          onChange={handleChangeModel}
          label="Select Model"
          SelectDisplayProps={{style: { color: '#e0f7fa' }}}
        >
          {binary.map((s, index) => (
            <MenuItem key={index} value={s} >
              {models[s]}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <FormControl variant="outlined" sx={{ minWidth: 200, mb: 2 }}>
        <InputLabel id="method-select-label" sx={{ color : "white"}}>Select Method</InputLabel>
        <Select
          labelId="method-select-label"
          value={selectedMethod}
          onChange={handleChangeMethod}
          label="Select Method"
          SelectDisplayProps={{style: { color: '#e0f7fa' }}}
        >
          {binary.map((s, index) => (
            <MenuItem key={index} value={s} >
              {methods[s]}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      
      <FormControl variant="outlined" sx={{ minWidth: 200, mb: 2 }}>
        <InputLabel id="portfolio-select-label" sx={{ color : "white"}}>Select Portfolio</InputLabel>
        <Select
          labelId="portfolio-select-label"
          value={selectedOption}
          onChange={handleChange}
          label="Select Portfolio"
          SelectDisplayProps={{style: { color: '#e0f7fa' }}}
        >
          {portfolios.map((s, index) => (
            <MenuItem key={index} value={s} >
              {s}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      </ThemeProvider>
      <Box>
        <VIXPlot data = {selectedData} VIX={vix} method={selectedMethod} />
      </Box>
    </Box>
  );
};

export default OptionBox;
